<template>
  <div class="amoutSearch">
    <v-container fluid >
      <!-- 標題列 -->
      <CommonBtnTitle
        :Title="$ln('額度查詢')"
        :BtnTitle="$ln('額度查詢')"
        :BtnFunc="search"
        :BtnParams="{
          disabled:amoutSearch.valid
        }"
      />

      <material-card>
        <v-form v-model="amoutSearch.valid">
          <!-- 日期 -->
          <CommonDatePicker
            :Title="$ln('日期')"
            :StartTime.sync="reportItems.countDate"
            :EndTime.sync="reportItems.countDateEnd"
            :CurrentClickDateBtn.sync="currentClickDateBtn"
            :QuickyBtn="[
              { name:'今天', type: 'Today' , func: ()=>setToday() },
              { name:'昨天', type: 'Yesterday' , func: ()=>setYesterday() },
              { name:'上週', type: 'LastWeek' , func: ()=>setLastWeek() },
              { name:'本週', type: 'ThisWeek' , func: ()=>setThisWeek() },
              { name:'上月', type: 'LastMonth' , func: ()=>setLastMonth() },
              { name:'本月', type: 'ThisMonth' , func: ()=>setThisMonth() },
            ]"
          />

          <!-- 查詢對象 -->
          <v-form v-model="amoutSearch.valid">
            <v-layout row wrap>
              <v-flex xl1 md2 xs12 class="pt-3">
                <label>{{ $ln('查詢對象') }}</label>
              </v-flex>
              <v-flex xl2 md3 xs12>
                <v-radio-group v-model="amoutSearch.stratumItems.selected" row>
                  <v-radio
                    class="v-input-checkbox"
                    v-for="(item, i) in amoutSearch.stratumItems.options"
                    v-show="item.show"
                    :key="i"
                    :label="$ln(item.text)"
                    :value="item.value"
                  />
                </v-radio-group>
              </v-flex>
            </v-layout>
          </v-form>
        </v-form>
      </material-card>

      <!-- 底部按鈕 -->
      <CommonBtnTitle
        Title=""
        :BtnTitle="$ln('額度查詢')"
        :BtnFunc="search"
        :BtnParams="{
          disabled:amoutSearch.valid
        }"
      />
    </v-container>
  </div>
</template>

<script>
import moment from 'moment'
import _merge from "lodash/merge";
import DateTime from "@/mixins/other/DateTime";

export default {
  name: "AmoutSearch",
  mixins:[DateTime],
  data() {
    return {
      // 當前已點擊的日期按鈕 (Today/ Yesterday/ LastWeek/ ThisWeek/ LastMonth/ ThisMonth)
      currentClickDateBtn: '',
      amoutSearch: {
        valid: false,
        stratumItems: {
          selected: 0,
          options: [
            {
              show: true,
              text: '代理',
              value: 1
            },
            {
              show: true,
              text: '會員',
              value: 0
            }
          ]
        }
      },
    };
  },
  watch: {
    '$store.state.lang' (value) {
      this.initLanguage()

      /* 初始日期選擇器設定 */
      // 開始日期
      this.flatpickrDate.startDate = flatpickr('[name="start-date"].date-picker', _merge(this.flatpickrConfig))
      this.flatpickrDate.startDate.setDate(this.reportItems.countDate)
      // 結束日期
      this.flatpickrDate.endDate = flatpickr('[name="end-date"].date-picker', _merge(this.flatpickrConfig))
      this.flatpickrDate.endDate.setDate(this.reportItems.countDateEnd)
    }
  },
  mounted () {
    this.initLanguage()

    this.initDate()
    this.initDateBtn()

    // 判斷層級
    if (parseInt(sessionStorage.getItem('super.manage.level')) <= 2) {
      this.amoutSearch.stratumItems.options[0].show = false
    }
  },
  methods: {
    search () {
      this.$router.push({
        path: "/manage/report/amout-search/Result",
        query: {
          time_start: this.reportItems.countDate,
          time_end: this.reportItems.countDateEnd,
          lv: this.amoutSearch.stratumItems.selected
          // click_acc: 'a51'
        }
      })
    },
    // 初始語系
    initLanguage () {
      // 日期選擇器設定
      this.flatpickrConfig.locale.weekdays = {
        shorthand: [
          this.$ln('週日'),
          this.$ln('週一'),
          this.$ln('週二'),
          this.$ln('週三'),
          this.$ln('週四'),
          this.$ln('週五'),
          this.$ln('週六')
        ],
        longhand: [
          this.$ln('星期日'),
          this.$ln('星期一'),
          this.$ln('星期二'),
          this.$ln('星期三'),
          this.$ln('星期四'),
          this.$ln('星期五'),
          this.$ln('星期六')
        ]
      }
      this.flatpickrConfig.locale.months = {
        shorthand: [
          this.$ln('一月'),
          this.$ln('二月'),
          this.$ln('三月'),
          this.$ln('四月'),
          this.$ln('五月'),
          this.$ln('六月'),
          this.$ln('七月'),
          this.$ln('八月'),
          this.$ln('九月'),
          this.$ln('十月'),
          this.$ln('十一月'),
          this.$ln('十二月')
        ],
        longhand: [
          this.$ln('一月'),
          this.$ln('二月'),
          this.$ln('三月'),
          this.$ln('四月'),
          this.$ln('五月'),
          this.$ln('六月'),
          this.$ln('七月'),
          this.$ln('八月'),
          this.$ln('九月'),
          this.$ln('十月'),
          this.$ln('十一月'),
          this.$ln('十二月')
        ]
      }
    }
  }
};
</script>

<style lang="scss">

</style>
